

.forms-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0; }
.signin-signup {
  position: absolute;
  top: 50%;
  left: 75%;
  transform: translate(-50%, -50%);
  width: 50%;
  display: grid;
  grid-template-columns: 1fr;
  z-index: 5;
  transition: 1s 0.7s ease-in-out; }
form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 5rem;
  overflow: hidden;
  grid-column: 1 / 2;
  grid-row: 1 / 2;
  transition: 0.2s 0.7s ease-in-out; }
form.sign-in-form {
  z-index: 2; }
form.sign-up-form {
  z-index: 1;
  opacity: 0; }


.title {
  font-size: 2.2rem;
  color: #444444;
  margin-bottom: 10px; }
.input-field {
  max-width: 380px;
  width: 100%;
  height: 55px;
  background-color: #f0f0f0;
  margin: 10px 0;
  border-radius: 55px;
  display: grid;
  grid-template-columns: 15% 85%;
  padding: 0 0.4rem;
  i {
    text-align: center;
    line-height: 55px;
    color: #acacac;
    font-size: 1.1rem; }
  input {
    background: none;
    outline: none;
    border: none;
    line-height: 1;
    font-weight: 600;
    font-size: 1.1rem;
    color: #333; } }
.btn {
  width: 150px;
  height: 49px;
  border: none;
  outline: none;
  border-radius: 49px;
  cursor: pointer;
  background-color: #5995fd;
  color: #fff;
  text-transform: uppercase;
  font-weight: 600;
  margin: 10px 0;
  transition: 0.5s;
  &:hover {
    background-color: #4d84e2; } }
.social-text {
  padding: 0.7rem 0;
  font-size: 1rem; }
.social-media {
  display: flex;
  justify-content: center; }
.social-icon {
  width: 46px;
  height: 46px;
  border: 1px solid #333333;
  margin: 0 0.45rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #333;
  font-size: 1.1rem;
  border-radius: 50%;
  transition: 0.3s;
  &:hover {
    color: #4d84e2;
    border-color: #4d84e2; } }


@media (max-width: 870px) {
  .container {
    div .signin-signup {
      width: 100%;
      left: 50%;
      top: 95%;
      transform: translate(-50%, -100%); } } }
