@import "fonts";
@import "libs";
@import "vars";

@import "_mixins/mixins";
@import "_mixins/mixinss";
@import "blocks/form-container";
@import "blocks/panels-container";




*,
::before,
::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0; }

body, input {
	font-family: $default-font;
	font-size: 14px;
	line-height: 1.4;
	margin: 0;
	padding: 0;
	color: #000;
	background-color: $white;

	// @media (min-width: 1440px)
 }	// 	@include dynamicText(103%, 18, 13, 1440px, 2880, 1440)

/** Общие стили **/
.container {
  position: relative;
  width: 100%;
  min-height: 100vh;
  background-color: #fff;
  overflow: hidden;
  &::before {
    content: ' ';
    position: absolute;
    width: 2000px;
    height: 2000px;
    border-radius: 50%;
    background: linear-gradient(-45deg, #4481eb, #04befe);
    top: -10%;
    right: 48%;
    transform: translateY(-50%);
    z-index: 6;
    transition: 1.8s ease-in-out; } }

/** animation **/

.sign-up-mode {
  &::before {
    transform: translate(100%, -50%);
    right: 52%; }
  .left-panel {
    pointer-events: none;
    .image, .content {
      transform: translateX(-800px); } }
  .right-panel {
    pointer-events: all;
    .content, .image {
      transform: translateX(0); } }
  .signin-signup {
    left: 25%; }
  .sign-in-form {
    z-index: 1;
    opacity: 0; }
  .sign-up-form {
    z-index: 2;
    opacity: 1; } }


@media (max-width: 870px) {
  .container {
    min-height: 800px;
    height: 100vh;
    &::before, {
      display: none; } } }

