


.panels-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr); }
.panel {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  text-align: center;
  z-index: 7;
  .content {
    color: #fff;
    transition: 0.9s 0.6s ease-in-out; }
  h3 {
    font-weight: 600;
    line-height: 1;
    font-size: 1.5rem; }
  p {
    font-size: 0.95rem;
    padding: 0.7rem; } }
.left-panel {
  pointer-events: all;
  padding: 3rem 17% 2rem 12%; }
.right-panel {
  pointer-events: none;
  padding: 3rem 12% 2rem 17%;
  .content {
    transform: translateX(800px); }
  .image {
    transform: translateX(800px); } }

.image {
  width: 100%;
  transition: 1.1s 0.6s ease-in-out; }


.transparent {
  margin: 0;
  background: none;
  border: 2px solid #fff;
  width: 130px;
  height: 41px;
  font-weight: 600;
  font-size: 0.8rem; }

@media (max-width: 870px) {
  .container {
    .panels-container {
      display: none; } } }
